  &>div {
    height: 100%;
    overflow: auto;
  }
  .ivu-form {
    width: 50%;
    margin: auto;
    padding: 4% 0;
  }
  .btn-cancel {
    border: 1px solid #d7dde4;
    background: #d7dde4;
    margin-left: 20px;
    &:hover {
      color: #57a3f3;
      background-color: #fff !important;
      border-color: #57a3f3 !important;
    }
  }
  .mt50 {
    margin-top: 5%;
  }
}
